import React from 'react';
import { Row, Col, Card, Select, Button, Divider } from 'antd';
import { withPropsAPI } from 'gg-editor';
import merge from 'lodash/merge';
import {
  NODE_SHAPE_UNDERLINE,
  NODE_SHAPE_RECTANGLE,
  NODE_SHAPE_ROUNDED_RECT,
  NODE_BORDER_TYPE_NONE,
  NODE_BORDER_TYPE_SOLID_LINE,
  NODE_BORDER_TYPE_DOTTED_LINE,
  NODE_BORDER_TYPE_DASHED_LINE,
} from '@common/constants';
import ColorSelector from '@common/ColorSelector';
import styles from './index.css';

class NodeDetail extends React.Component {
  constructor(props) {
    super(props);

    const { nodeProps, rectStyle, textStyle } = this.model;
    const { nodeShape, nodeBorderType } = nodeProps;
    const { fill: nodeColor, lineWidth: nodeBorderWidth, stroke: nodeBorderColor } = rectStyle;
    const { fill: fontColor, fontSize, fontWeight, fontStyle } = textStyle;

    this.state = {
      nodeShape,
      nodeBorderType,
      nodeColor,
      nodeBorderWidth,
      nodeBorderColor,
      fontSize,
      fontColor,
      fontBold: fontWeight === 'bold',
      fontItalic: fontStyle === 'italic',
    };
  }

  get item() {
    const { getSelected } = this.props.propsAPI;

    return getSelected()[0];
  }

  get model() {
    return this.item.getModel();
  }

  handleUpdate = (model) => {
    const { executeCommand } = this.props.propsAPI;
    const { nodeProps, rectStyle, textStyle } = merge({}, this.model, model);

    executeCommand('update', {
      action: 'updateStyle',
      itemId: this.item.id,
      updateModel: {
        nodeProps,
        rectStyle,
        textStyle,
      },
    });
  }

  handleUpdateNodeProps = (props) => {
    this.handleUpdate({
      nodeProps: props,
    });
  }

  handleUpdateRectStyle = (style) => {
    this.handleUpdate({
      rectStyle: style,
    });
  }

  handleUpdateTextStyle = (style) => {
    this.handleUpdate({
      textStyle: style,
    });
  }

  handleFontBold = () => {
    const { fontBold } = this.state;

    this.handleUpdateTextStyle({
      fontWeight: fontBold ? 'normal' : 'bold',
    });

    this.setState({
      fontBold: !fontBold,
    });
  }

  handleFontItalic = () => {
    const { fontItalic } = this.state;

    this.handleUpdateTextStyle({
      fontStyle: fontItalic ? 'normal' : 'italic',
    });

    this.setState({
      fontItalic: !fontItalic,
    });
  }

  renderNodeShapeSelector() {
    const { nodeShape, nodeBorderType } = this.state;

    return (
      <Select
        className={styles.select}
        value={nodeShape}
        onChange={(value) => {
          const props = {
            nodeShape: value,
          };

          if (value === NODE_SHAPE_UNDERLINE) {
            props.nodeBorderType = NODE_BORDER_TYPE_NONE;
          } else if (nodeBorderType === NODE_BORDER_TYPE_NONE) {
            props.nodeBorderType = NODE_BORDER_TYPE_SOLID_LINE;
          }

          this.handleUpdateNodeProps(props);
          this.setState(props);
        }}
      >
        <Select.Option value={NODE_SHAPE_ROUNDED_RECT}>圆角矩形</Select.Option>
        <Select.Option value={NODE_SHAPE_RECTANGLE}>矩形</Select.Option>
        <Select.Option value={NODE_SHAPE_UNDERLINE} disabled={this.item.isRoot}>下划线</Select.Option>
      </Select>
    );
  }

  renderNodeColorSelector() {
    const { nodeColor } = this.state;

    return (
      <ColorSelector
        defaultColor="#ffffff"
        color={nodeColor}
        onChange={({ hex }) => {
          this.handleUpdateRectStyle({ fill: hex });
        }}
      />
    );
  }

  renderNodeBorderTypeSelector() {
    const { nodeBorderType } = this.state;

    return (
      <Select
        className={styles.select}
        value={nodeBorderType === NODE_BORDER_TYPE_NONE ? '无' : nodeBorderType}
        disabled={nodeBorderType === NODE_BORDER_TYPE_NONE}
        onChange={(value) => {
          this.handleUpdateNodeProps({
            nodeBorderType: value,
          });
        }}
      >
        <Select.Option value={NODE_BORDER_TYPE_SOLID_LINE}>实线</Select.Option>
        <Select.Option value={NODE_BORDER_TYPE_DOTTED_LINE}>虚线</Select.Option>
        <Select.Option value={NODE_BORDER_TYPE_DASHED_LINE}>点划线</Select.Option>
      </Select>
    );
  }

  renderNodeBorderWidthSelector() {
    const { nodeBorderType, nodeBorderWidth = 0 } = this.state;

    return (
      <Select
        className={styles.select}
        value={nodeBorderWidth}
        disabled={nodeBorderType === NODE_BORDER_TYPE_NONE}
        onChange={(value) => {
          this.handleUpdateRectStyle({
            lineWidth: value,
          });
        }}
      >
        {[1, 2, 3, 4, 5, 6].map(value => <Select.Option value={value}>{value}</Select.Option>)}
      </Select>
    );
  }

  renderNodeBorderColorSelector() {
    const { nodeBorderType, nodeBorderColor } = this.state;

    return (
      <ColorSelector
        defaultColor="#ffffff"
        color={nodeBorderColor}
        disabled={nodeBorderType === NODE_BORDER_TYPE_NONE}
        onChange={({ hex }) => {
          this.handleUpdateRectStyle({ stroke: hex });
        }}
      />
    );
  }

  renderFontFamilySelector() {
    return (
      <Select className={styles.select} value="">
        <Select.Option value="">字体</Select.Option>
      </Select>
    );
  }

  renderFontSizeSelector() {
    const { fontSize } = this.state;

    return (
      <Select
        className={styles.select}
        value={fontSize}
        onChange={(value) => {
          value = parseInt(value, 10);

          this.handleUpdateTextStyle({
            fontSize: value,
          });
        }}
      >
        {[12, 14, 16, 20, 24, 30, 38, 46, 56, 68].map(value => <Select.Option value={value}>{value}</Select.Option>)}
      </Select>
    );
  }

  renderFontStyleButtonGroup() {
    const { fontBold, fontItalic } = this.state;

    return (
      <Button.Group className={styles.buttonGroup}>
        <Button
          type={fontBold ? 'primary' : ''}
          icon="bold"
          onClick={this.handleFontBold}
        />
        <Button
          type={fontItalic ? 'primary' : ''}
          icon="italic"
          onClick={this.handleFontItalic}
        />
      </Button.Group>
    );
  }

  renderFontColorSelector() {
    const { fontColor } = this.state;
    const { isRoot } = this.item;

    return (
      <ColorSelector
        defaultColor={isRoot ? '#ffffff' : '#000000'}
        color={fontColor}
        onChange={({ hex }) => {
          this.handleUpdateTextStyle({ fill: hex });
        }}
      />
    );
  }

  render() {
    return (
      <Card type="inner" title="节点" bordered={false} className={styles.nodeDetail}>
        <h4>形状</h4>
        <Row>
          <Col span={16}>
            {this.renderNodeShapeSelector()}
          </Col>
          <Col span={7} offset={1}>
            {this.renderNodeColorSelector()}
          </Col>
        </Row>
        <Divider />
        <h4>边缘</h4>
        <Row>
          <Col span={8}>
            {this.renderNodeBorderTypeSelector()}
          </Col>
          <Col span={7} offset={1}>
            {this.renderNodeBorderWidthSelector()}
          </Col>
          <Col span={7} offset={1}>
            {this.renderNodeBorderColorSelector()}
          </Col>
        </Row>
        <Divider />
        <h4>字体</h4>
        <Row>
          <Col span={16}>
            {this.renderFontFamilySelector()}
          </Col>
          <Col span={7} offset={1}>
            {this.renderFontSizeSelector()}
          </Col>
        </Row>
        <Row>
          <Col span={16}>
            {this.renderFontStyleButtonGroup()}
          </Col>
          <Col span={7} offset={1}>
            {this.renderFontColorSelector()}
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withPropsAPI(NodeDetail);
