
import React from 'react';
import { RegisterCommand } from 'gg-editor';

class UMindSave extends React.Component {
  render() {
    const { save } = this.props;

    const config = {
      queue: false,

      enable() {
        return true;
      },

      execute() {
        save();
      },

      shortcutCodes: [
        ['metaKey', 's'],
        ['ctrlKey', 's'],
      ],
    };

    return <RegisterCommand name="save" config={config} />;
  }
}

export default UMindSave;
