import React from 'react';
import { Tooltip } from 'antd';
import { Command } from 'gg-editor';
import iconfont from '@theme/iconfont.css';
import styles from './index.css';

class Button extends React.Component {
  render() {
    const { name, icon, text, hotkey } = this.props;

    return (
      <Command name={name}>
        <Tooltip title={hotkey} placement="bottom" overlayClassName={styles.tooltip}>
          <div className={`${styles.button}`}>
            <i className={`${styles.buttonIcon} ${iconfont.iconfont} ${iconfont[icon]}`} />
            <span className={styles.buttonText}>{text}</span>
          </div>
        </Tooltip>
      </Command>
    );
  }
}

export default Button;
