import React from 'react';
import { Divider } from 'antd';
import { Toolbar } from 'gg-editor';
import Button from './Button';
import styles from './index.css';

class UMindToolbar extends React.Component {
  render() {
    return (
      <Toolbar className={styles.toolbar}>
        <Button name="save" icon="iconSave" text="保存" hotkey="⌘+S" />
        <Divider type="vertical" />
        <Button name="undo" icon="iconUndo" text="撤销" hotkey="⌘+Z" />
        <Button name="redo" icon="iconRedo" text="重做" hotkey="⌘+Shift+Z" />
        <Divider type="vertical" />
        <Button name="append" icon="iconAppend" text="插入同级" hotkey="Enter" />
        <Button name="appendChild" icon="iconAppendChild" text="插入子级" hotkey="Tab" />
        <Divider type="vertical" />
        <Button name="collapse" icon="iconCollapse" text="折叠" hotkey="⌘+/" />
        <Button name="expand" icon="iconExpand" text="展开" hotkey="⌘+/" />
      </Toolbar>
    );
  }
}

export default UMindToolbar;
