import React from 'react';
import { Row, Col } from 'antd';
import GGEditor, { Mind, RegisterNode, RegisterEdge } from 'gg-editor';
import Toolbar from '@components/Toolbar';
import Sidebar from '@components/Sidebar';
import ContextMenu from '@components/ContextMenu';
import UMindRoot from '@umind/shape/nodes/UMindRoot';
import UMindNode from '@umind/shape/nodes/UMindNode';
import UMindEdge from '@umind/shape/edges/UMindEdge';
import UMindSave from '@umind/command/UMindSave';
import styles from './index.css';

class UMind extends React.Component {
  handleSave = () => {
    const { save } = this.props;

    if (save) {
      save(this.mind.save());
    }
  }

  handleAfterCommandExecute = ({ command }) => {
    const { name } = command;

    if (name !== 'save') {
      this.handleSave();
    }

    this.mind.focusGraphWrapper();
  }

  render() {
    const { data } = this.props;

    return (
      <GGEditor
        className={styles.editor}
        onAfterCommandExecute={this.handleAfterCommandExecute}
      >
        <Row type="flex" className={styles.editorHd}>
          <Col span={24}>
            <Toolbar />
          </Col>
        </Row>
        <Row type="flex" className={styles.editorBd}>
          <Col span={24} className={styles.editorContent}>
            <Mind
              ref={(component) => {
                if (component) {
                  this.mind = component.page;
                }
              }}
              className={styles.mind}
              data={data}
              firstSubShape="mind-node"
              secondSubShape="mind-node"
            />
            <Sidebar />
          </Col>
        </Row>
        <ContextMenu />
        <RegisterNode name="mind-node" config={UMindNode} extend="mind-node" />
        <RegisterNode name="mind-root" config={UMindRoot} extend="mind-node" />
        <RegisterEdge name="mind-edge" config={UMindEdge} extend="mind-edge" />
        <UMindSave save={this.handleSave} />
      </GGEditor>
    );
  }
}

export default UMind;
