import {
  ROOT_NODE_PROPS,
  ROOT_RECT_STYLE,
  ROOT_TEXT_STYLE,
  SECOND_NODE_PROPS,
  SECOND_RECT_STYLE,
  SECOND_TEXT_STYLE,
  NORMAL_NODE_PROPS,
  NORMAL_RECT_STYLE,
  NORMAL_TEXT_STYLE,
} from '@umind/theme/default';

import {
  NODE_SHAPE_STYLE_MAP,
  NODE_BORDER_TYPE_STYLE_MAP,
  FONT_SIZE_STYLE_MAP,
  mixStyle,
} from '@umind/style';

const NODE_PROPS_MAP = new Map([[1, ROOT_NODE_PROPS], [2, SECOND_NODE_PROPS], [3, NORMAL_NODE_PROPS]]);
const RECT_STYLE_MAP = new Map([[1, ROOT_RECT_STYLE], [2, SECOND_RECT_STYLE], [3, NORMAL_RECT_STYLE]]);
const TEXT_STYLE_MAP = new Map([[1, ROOT_TEXT_STYLE], [2, SECOND_TEXT_STYLE], [3, NORMAL_TEXT_STYLE]]);

export default {
  drawLabel(item) {
    const group = item.getGraphicGroup();

    this.containerGroup = group.addGroup();

    this.mixNodeProps(item);

    this.drawRect(item);
    this.drawText(item);
    this.drawIcon(item);
    this.drawMark(item);
    this.drawLink(item);

    this.adjustRect();
    this.adjustText();
    this.adjustContainerGroup();

    return this.containerGroup;
  },

  drawRect(item) {
    const { nodeProps } = item.getModel();
    const rectStyle = this.getRectStyle(item);

    this.rectShape = this.containerGroup.addShape('rect', {
      attrs: mixStyle({
        x: 0,
        y: 0,
        width: 0,
        height: 0,
        ...rectStyle,
      }, NODE_BORDER_TYPE_STYLE_MAP[nodeProps.nodeBorderType]),
    });
  },

  drawText(item) {
    const { label } = item.getModel();
    const textStyle = this.getTextStyle(item);

    this.textShape = this.containerGroup.addShape('text', {
      class: 'label',
      attrs: mixStyle({
        x: 0,
        y: 0,
        text: label,
        textAlign: 'left',
        textBaseline: 'middle',
        ...textStyle,
      }, FONT_SIZE_STYLE_MAP[textStyle.fontSize]),
    });
  },

  drawIcon() {},

  drawMark() {},

  drawLink() {},

  adjustRect() {
    const { width } = this.containerGroup.getBBox();
    const [ph, pv] = this.getPadding();
    const lineHeight = this.textShape.attr('lineHeight');

    const w = width + ph * 2;
    const h = lineHeight + pv * 2;

    this.rectShape.attr({
      width: w,
      height: h,
    });
  },

  adjustText() {
    const { height } = this.textShape.getBBox();
    const [ph, pv] = this.getPadding();

    const x = ph;
    const y = (height / 2) + pv + 4;

    this.textShape.translate(x, y);
  },

  adjustContainerGroup() {
    const { width, height } = this.containerGroup.getBBox();
    const lineWidth = this.rectShape.attr('lineWidth') || 0;

    const x = -(width / 2) + (lineWidth / 2);
    const y = -(height / 2) + (lineWidth / 2) + 4;

    this.containerGroup.translate(x, y);
  },

  getSize() {
    const { width, height } = this.containerGroup.getBBox();

    const w = width + 2;
    const h = height + 2;

    return [w, h];
  },

  getPadding() {
    return [8, 4];
  },

  mixNodeProps(item) {
    const model = item.getModel();
    const { nodeProps, hierarchy } = model;

    if (!nodeProps) {
      model.nodeProps = NODE_PROPS_MAP.get(hierarchy) || NODE_PROPS_MAP.get(3);
    }
  },

  getRectStyle(item) {
    const model = item.getModel();
    const { nodeProps, rectStyle, hierarchy } = model;
    const { nodeShape } = nodeProps;

    if (!rectStyle) {
      model.rectStyle = RECT_STYLE_MAP.get(hierarchy) || RECT_STYLE_MAP.get(3);
    }

    mixStyle(model.rectStyle, NODE_SHAPE_STYLE_MAP[nodeShape]);

    return model.rectStyle;
  },

  getTextStyle(item) {
    const model = item.getModel();
    const { textStyle, hierarchy } = model;

    if (!textStyle) {
      model.textStyle = TEXT_STYLE_MAP.get(hierarchy) || TEXT_STYLE_MAP.get(3);
    }

    return model.textStyle;
  },
};
