import React from 'react';
import { CustomPicker } from 'react-color';
import styles from './index.css';

const COLORS = [
  ['#000000', '#262626', '#595959', '#8c8c8c', '#bfbfbf', '#d9d9d9', '#e8e8e8', '#f5f5f5', '#fafafa', '#ffffff'],
  ['#f5222d', '#fa541c', '#fa8c16', '#fadb14', '#52c41a', '#13c2c2', '#1890ff', '#2f54eb', '#722ed1', '#eb2f96'],
  ['#ffe8e6', '#ffece0', '#ffefd1', '#fcfcca', '#e4f7d2', '#d3f5f0', '#d4eefc', '#dee8fc', '#efe1fa', '#fae1eb'],
  ['#ffa39e', '#ffbb96', '#ffd591', '#fffb8f', '#b7eb8f', '#87e8de', '#91d5ff', '#adc6ff', '#d3adf7', '#ffadd2'],
  ['#ff4d4f', '#ff7a45', '#ffa940', '#ffec3d', '#73d13d', '#36cfc9', '#40a9ff', '#597ef7', '#9254de', '#f759ab'],
  ['#cf1322', '#d4380d', '#d46b08', '#d4b106', '#389e0d', '#08979c', '#096dd9', '#1d39c4', '#531dab', '#c41d7f'],
  ['#820014', '#871400', '#873800', '#876800', '#135200', '#00474f', '#003a8c', '#061178', '#22075e', '#780650'],
];

class ColorBoard extends React.Component {
  static defaultProps = {
    defaultColor: '#ffffff',
    color: '#ffffff',
  }

  handleClick(color) {
    this.props.onChange({
      hex: color,
      source: 'hex',
    });
  }

  renderColor(color) {
    const { defaultColor, hex, hsl } = this.props;

    const isActive = color === hex;
    const isBright = hsl.l > 0.85 && isActive;
    const isDefault = color === defaultColor;

    const className = [styles.color];

    if (color === '#fafafa' || color === '#ffffff') {
      className.push(styles.border);
    }

    if (isActive) {
      className.push(styles.active);
    }

    if (isBright) {
      className.push(styles.bright);
    }

    if (isDefault) {
      className.push(styles.default);
    }

    return (
      <div className={className.join(' ')} onClick={() => { this.handleClick(color); }}>
        <div className={styles.colorBd} style={{ background: color }}>
          <svg viewBox="0 0 18 18">
            <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
          </svg>
        </div>
      </div>
    );
  }

  renderGroup(group) {
    return (
      <div className={styles.group}>
        {group.map(color => this.renderColor(color))}
      </div>
    );
  }

  render() {
    return (
      <div className={styles.colorBoard}>
        {COLORS.map((group, index) => this.renderGroup(group, index))}
      </div>
    );
  }
}

export default CustomPicker(ColorBoard);
