import React from 'react';
import { Popover } from 'antd';
import ColorBoard from '@common/ColorBoard';
import styles from './index.css';

class ColorSelector extends React.Component {
  static defaultProps = {
    color: '#ffffff',
    colorSelectorVisible: false,
    disabled: false,
  }

  state = {
    color: this.props.color,
  }

  handleChange = (data) => {
    const { onChange } = this.props;
    const { hex } = data;

    this.setState({
      color: hex,
      colorSelectorVisible: false,
    });

    if (onChange) {
      onChange(data);
    }
  }

  handleVisibleChange = (visible) => {
    const { disabled } = this.props;

    this.setState({
      colorSelectorVisible: disabled ? false : visible,
    });
  }

  renderColorBoard() {
    const { defaultColor } = this.props;
    const { color } = this.state;

    return (
      <ColorBoard defaultColor={defaultColor} color={color} onChange={this.handleChange} />
    );
  }

  render() {
    const { color, colorSelectorVisible } = this.state;

    return (
      <Popover
        content={this.renderColorBoard()}
        trigger="click"
        placement="bottomRight"
        visible={colorSelectorVisible}
        onVisibleChange={this.handleVisibleChange}
      >
        <div className={styles.colorSelector}>
          <div className={styles.colorSelectorBd} style={{ background: color }} />
        </div>
      </Popover>
    );
  }
}

export default ColorSelector;
